import { ref } from 'vue';

export function useProductSlider(batchSize = 5) {
  const currentPosition = ref(0);
  const slideDistance = ref(310); //   + 
  const isSliding = ref(false);

  const itemsContainer = ref<HTMLElement | null>(null);
  const contentCards = ref<HTMLElement | null>(null);

  const products = ref<any[]>([]);
  let loadedCount = 0;

  const updateWidth = (width: number) => {
    slideDistance.value = width + 10; //  + 
  };

  const loadMore = (allProducts: any[]) => {
    const nextBatch = allProducts.slice(loadedCount, loadedCount + batchSize);
    products.value.push(...nextBatch);
    loadedCount += nextBatch.length;
  };

  const loadMoreIfNeeded = (allProducts: any[]) => {
    const visibleIndex = Math.floor(Math.abs(currentPosition.value) / slideDistance.value) + 5;
    if (loadedCount < allProducts.length && visibleIndex >= products.value.length) {
      loadMore(allProducts);
    }
  };

  const slideLeft = (allProducts: any[]) => {
    isSliding.value = true;

    currentPosition.value += slideDistance.value; 
  
    if (currentPosition.value > 0) currentPosition.value = 0; 
  
    loadMoreIfNeeded(allProducts);
    setTimeout(() => (isSliding.value = false), 300);
  };
  
  const slideRight = (allProducts: any[]) => {
    isSliding.value = true;

    currentPosition.value -= slideDistance.value; 
  
    const maxTranslate = -(products.value.length - 1) * slideDistance.value;
    if (currentPosition.value < maxTranslate) currentPosition.value = maxTranslate; 
  
    loadMoreIfNeeded(allProducts);
    setTimeout(() => (isSliding.value = false), 300);
  };
  

  return {
    currentPosition,
    slideDistance,
    isSliding,
    itemsContainer,
    contentCards,
    products,
    updateWidth,
    slideLeft,
    slideRight,
    loadMoreIfNeeded,
  };
}
