<script setup lang="ts">
import { SliderCards } from '@/features/slider-cards';
import { Slider } from '@/shared/ui/slider';
import { useSwipe } from '@/shared/composables/useTouchSwipe';
import { onMounted, ref } from 'vue';
import axios from 'axios';
import { API_BASE_URL } from '@/shared/api';
import type { Product } from '@/entities/product/model/product.types';
import { useProductSlider } from '@/shared/composables/useProductSlider';
import { Button } from '@/shared/ui/button';
import { Container } from '@/shared/ui/container';
import { Heading } from '@/shared/ui/text/heading';

const allProducts = ref<Product[]>([]);

const {
  currentPosition,
  slideDistance,
  isSliding,
  itemsContainer,
  contentCards,
  products,
  updateWidth,
  slideLeft,
  slideRight,
  loadMoreIfNeeded
} = useProductSlider(5); 

const swipeStartPosition = ref(0);

useSwipe({
  elementRef: itemsContainer,
  onSwipeMove: (deltaX) => {
    const speedMultiplier = 1.2;
    currentPosition.value = swipeStartPosition.value + deltaX * speedMultiplier;
    if (currentPosition.value <= -1000) {
      loadMoreIfNeeded(allProducts.value);
    }
  },
  onSwipeEnd: (deltaX) => {
    const speedMultiplier = 1.2;
    swipeStartPosition.value += deltaX * speedMultiplier;
  }
});

defineExpose({ slideLeft, slideRight });

onMounted(async () => {
  try {
    const response = await axios.get(`${API_BASE_URL}products`);
    const productsData: Product[] = Array.isArray(response.data.results) ? response.data.results : [];

    allProducts.value = productsData.filter((p) =>
      p.categories.some((c) => c.slug === 'podarki')
    );

    products.value.push(...allProducts.value.slice(0, 5));
  } catch (error) {
    console.error('   :', error);
  }
});
</script>

<template>
  <Container>
    <div class="section__tittle-with-buttons">
      <Heading tagName="h2" regular size="xl"></Heading>
      <div class="buttons">
        <Button tagName="button" size="xs" typeButton="circle" @click="slideLeft(allProducts)"></Button>
        <Button tagName="button" size="xs" typeButton="circle" @click="slideRight(allProducts)"></Button>
      </div>
    </div>
  </Container>

  <Slider>
    <div class="podarki-slider__container" ref="itemsContainer" :style="{ transform: `translateX(${currentPosition}px)` }"
      :class="{ 'is-sliding': isSliding }">
      <div class="slider__items" ref="contentCards">
        <SliderCards v-for="product in products" :key="product.id" :cardData="product" @updateWidth="updateWidth" />
      </div>
    </div>
  </Slider>
</template>


<style scoped>
.section__tittle-with-buttons{
  display: flex;
  justify-content: space-between;
}
.buttons {
  display: flex;
  gap: 10px;
  margin-top: 15px;
}

.podarki-slider__container {
  display: flex;
  touch-action: pan-y;
  -webkit-overflow-scrolling: touch;

}

.slider__items {
  display: flex;
  gap: 10px;
}

.is-sliding {
  transition: transform 0.3s ease;
}
@media screen and (max-width: 769px) {

  .buttons {
  display: none;
}
}
</style>
